
.content {
    padding-top: 40px;
}

.headerText{
    text-align: center;
    margin-top: 10px;
}

.buttons {
    text-align: center;
}

.buttons button{
    margin: 40px;
}

* {
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  color: #333;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  line-height: 1.6; }

.header {
  background: #20232a; }

.header__title {
  color: white;
  text-decoration: none; }
  .header__title h1 {
    margin: 0; }

.header--link {
  color: #f7f7f7; }

.login--form {
  width: 50%;
  margin: 0 auto;
  margin-top: 10%; }

.login--inner {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 1.2rem; }

.input--login {
  font-size: 100%;
  margin-bottom: 15px; }

.login--title {
  text-align: center;
  letter-spacing: 1px;
  margin-bottom: 10px; }

html, body {
  width: 100%;
  height: 100%;
  position: absolute; }

.side_section {
  background-color: #4e555b; }

.side_section {
  color: white; }

.submit_button {
  text-align: center; }

.side_section_date {
  font-size: 1.3em;
  padding-top: 10px; }

.side_section_user_data {
  padding-top: 30px; }

.side_section_stats {
  padding-top: 50px;
  padding-bottom: 50px; }

.side_section_stats h2 {
  font-size: 1.2em; }

.section_header {
  font-size: 2em;
  padding-bottom: 20px;
  padding-top: 40px; }

.award_table thead {
  font-size: .85em;
  font-weight: bold; }

.award_data th {
  font-weight: normal; }

.input_width {
  width: 100%; }

